import React, { useEffect, useState } from "react";
import { Link, createSearchParams, useNavigate } from "react-router-dom";

function HttpsDemo1() {
  const [productData, setProductData] = useState([]);

  let fetchProductData = async function () {
    let data1 = await fetch("https://fakestoreapi.com/products");
    let fData = await data1.json();
    setProductData(fData);
  };

//   fetchProductData();

  useEffect(() => {
    fetchProductData();
  }, []);

  const navgate = useNavigate();

  const navigateHandler = (title, price) => {
    navgate({
      pathname: '/product-detail',
      search:`?${createSearchParams({title, price})}`,
      
    });
  }

  return (
    <>
     <div className="container">
      <div className="row">
        {productData.map((ele, index) => {
          return (
            <div className="col-md-3" key={index}>
              {/* Card Section Start */}
              <div className="card" style={{ width: "18rem" }}>
                <img src={ele.image} className="card-img-top" alt="..." />
                <div className="card-body">
                  <h5 className="card-title">{ele.title}</h5>
                  <p className="card-text">
                    {ele.price}
                  </p>
                  <Link to={`/product-details/${ele.id}`}>
                    <button className="btn btn-info text-white">View</button>
                  </Link>

                  <button
                  onClick={()=>{
                   navigateHandler(ele.title, ele.price);
                  }}
                   className="btn btn-primary">View - 2</button>

                   
                </div>
              </div>
              {/* Card Section End */}
            </div>
          );
        })}
      </div>
      </div>
    </>
  );
}
export default HttpsDemo1;
